import React from 'react'
import { Icon } from '@iconify/react';
import './team.scss'
import { Link } from 'react-router-dom';
import Div from '../Div';
import parse from 'html-react-parser';

export default function Team({memberID, memberImage, memberName, memberDesignation, memberSocial, memberInfo, alive}) {
  return (
    <Div className="cs-team cs-style1">
      <Link to={
        memberInfo ? "../members-info/"+memberID : ''}>
        <Div className="cs-member_thumb">
          <img src={memberImage} alt={memberName} />
          <Div className="cs-member_overlay" />
        </Div>
        <Div className="cs-member_info">
          <h2 className="cs-member_name">{memberName}{alive === false ? <Icon icon="emojione-monotone:reminder-ribbon" color="#000" /> : ''}</h2>
          <Div className="cs-member_designation">{parse(memberDesignation)}</Div>
        </Div>
      </Link>
        <Div className="cs-member_social cs-primary_color">
          {memberSocial.website && (
            <a href={memberSocial.website}>
              <Icon icon="mdi:web" />                   
            </a>
          )}
          {memberSocial.twitter && (
            <a href={memberSocial.twitter}>
              <Icon icon="fa-brands:twitter" />                                        
            </a>
          )}
          {memberSocial.youtube && (
            <a href={memberSocial.youtube}>
              <Icon icon="fa-brands:youtube" />                     
            </a>
          )}
          {memberSocial.facebook && (
            <a href={memberSocial.facebook}>
              <Icon icon="fa-brands:facebook-f" />                    
            </a>
          )}
       </Div>
      </Div>
  )
}
