import { Icon } from '@iconify/react';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { pageTitle } from '../../helper';
import Cta from '../Cta';
import PageHeading from '../PageHeading';
import MStatus from '../Status';
import Div from '../Div';
import SectionHeading from '../SectionHeading';
import Spacing from '../Spacing';

export default function StatusPage() {
  pageTitle('Status');
  const [active, setActive] = useState('all');
  const [itemShow, setItemShow] = useState(6);
  const StatusData = [
    {
      title: ' - Flatout',
      subtitle: '2600ML63 AMG',
      href: '/status/status01',
      src: '/images/status01.jpeg',
      category: 'All',
    },
  ];

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <>
      <PageHeading
        title=""
        bgSrc="images/portfolio_hero_bg.jpeg"
        pageLinkText=""
      />
      <Spacing lg="145" md="80" />
      <Div className="container">
        <Div className="row">
          {StatusData.slice(0, itemShow).map((item, index) => (
            <Div
              className={`${
                index === 0 || (((index % 5) === 0 && (index % 10 !== 0) )|| (index > 10 && (index % 10) === 1)) ? 'col-lg-8' : 'col-lg-4'
              } ${
                active === 'all'
                  ? ''
                  : !(active === item.category)
                  ? 'd-none'
                  : ''
              }`}
              key={index}
            >
              <MStatus
                title={item.title}
                subtitle={item.subtitle}
                href={item.href}
                src={item.src}
                variant="cs-style1 cs-type1"
              />
              <Spacing lg="25" md="25" />
            </Div>
          ))}
        </Div>

        <Div className="text-center">
          {StatusData.length <= itemShow ? (
            ''
          ) : (
            <>
              <Spacing lg="65" md="40" />
              <span
                className="cs-text_btn"
                onClick={() => setItemShow(itemShow + 3)}
              >
                <span></span>
                <Icon icon="bi:arrow-right" />
              </span>
            </>
          )}
        </Div>
      </Div>
      <Spacing lg="70" md="80" />
    </>
  );
}
